import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import './Header.css';


class Header extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    
  }

  render(){
    return(
      <body class="container">
    		<div class="header">
          {this.props.pageNum != 0 && <Link class = "header_text" to="/">JASON XU</Link>}
    			{this.props.pageNum == 0 && <Link id="current_page" class = "header_text" to="/">JASON XU</Link>}
    			<div class = "menu">
    				{this.props.pageNum != 1 && <Link class = "header_text" to="/education">EDUCATION</Link>}
            {this.props.pageNum == 1 && <Link id="current_page" class = "header_text" to="/education">EDUCATION</Link>}
            {this.props.pageNum != 2 && <Link class = "header_text" to="/projects">PROJECTS</Link>}
            {this.props.pageNum == 2 && <Link id="current_page" class = "header_text" to="/projects">PROJECTS</Link>}
            {this.props.pageNum != 3 && <Link class = "header_text" to="/experience">EXPERIENCE</Link>}
            {this.props.pageNum == 3 && <Link id="current_page" class = "header_text" to="/experience">EXPERIENCE</Link>}
            {this.props.pageNum != 4 && <Link class = "header_text" to="/blog">BLOG</Link>}
            {this.props.pageNum == 4 && <Link id="current_page" class = "header_text" to="/blog">BLOG</Link>}
    			</div>
    		</div>
    	</body>
    )
  }
}

export default Header;
