import React, { createRef, Component } from "react";
import * as d3 from "d3";

class PieClass extends Component {
  constructor(props) {
    super(props);
    this.ref = createRef();
    this.createPie = d3
      .pie()
      .value(d => parseInt(d.PriceTotal))
      .sort(null);
    this.createArc = d3
      .arc()
      .innerRadius(props.innerRadius)
      .outerRadius(props.outerRadius);
    this.colors = d3.scaleOrdinal(d3.schemeCategory10);
    this.format = d3.format(".2f");
    this.state = {
      data: this.props.data,
      updatedYet: false
    }
  }

  initialSetup(data){
    const svg = d3.select(this.ref.current);
    const { width, height, innerRadius, outerRadius } = this.props;

    svg
      .attr("class", "chart")
      .attr("width", width)
      .attr("height", height);

    const group = svg
      .append("g")
      .attr("transform", `translate(${outerRadius} ${outerRadius})`);

    const groupWithEnter = group
      .selectAll("g.arc")
      .data(data)
      .enter();

    const path = groupWithEnter.append("g").attr("class", "arc");

    path
      .append("path")
      .attr("class", "arc")
      .attr("d", this.createArc)
      .attr("fill", (d, i) => this.colors(d.index));
    //labeller
    path
      .append("text")
      .attr("text-anchor", "middle")
      .attr("alignment-baseline", "middle")
      .attr("transform", d => `translate(${this.createArc.centroid(d)})`)
      .style("fill", "white")
      .style("font-size", 10)
      .text(d => `${d.data.Category}`);
  }

  updating(data){
    var Tooltip = d3.select(".hi")
      .append("div")
      .style("opacity", 0)
      .attr("class", "tooltip")
      .style("background-color", "white")
      .style("width", "200px")
      .style("border", "solid")
      .style("border-width", "2px")
      .style("border-radius", "5px")
      .style("padding", "5px")
      .style("text-align","center")
      .style("Position","absolute")
      
    const svg = d3.select(this.ref.current);

    const group = svg
      .select("g")
      .selectAll("g.arc")
      .data(data);

    group.exit().remove();

    const groupWithUpdate = group
      .enter()
      .append("g")
      .attr("class", "arc")
      .on("mouseover", () => {
        Tooltip
          .style("opacity", 1)
      })
      .on("mousemove", (d) => {
        Tooltip
          .html(d.data.Category+"<br />"+d.data.PriceTotal)
        console.log(d.data.Category);
      })
      .on("mouseleave", (d) =>{
        Tooltip
          .style("opacity", 0)
      })

    const path = groupWithUpdate.append("path").merge(group.select("path.arc"));
    path
      .attr("class", "arc")
      .attr("d", this.createArc)
      .attr("fill", (d, i) => this.colors(i));
    const text = groupWithUpdate
                    .append("text").merge(group.select("text"));
      
    text
      .attr("text-anchor", "middle")
      .attr("alignment-baseline", "middle")
      .attr("transform", d => `translate(${this.createArc.centroid(d)})`)
      .attr("font-size","15px")
      .text(d => `${d.data.Category}`);
  }

  clear(){
    const svg = d3.select(this.ref.current)
    svg.destroy();
  }

  componentDidMount() {
    const data = this.createPie(this.props.data);
    this.initialSetup(data);
    this.state.data = this.props.data;
    console.log("this is the data")
    this.forceUpdate();
  }

  componentWillUpdate(nextProps, nextState) {
    if(this.state.updatedYet){
      this.updating(this.state.data);
    }else{
      const data = this.createPie(this.props.data);
      this.updating(data);
    }
    
  }


  update(newData){
    console.log("hi");
    console.log(newData);
    this.setState({ data: newData });
    this.setState({ updatedYet: true });
    const data = this.createPie(newData);
    this.clear();
    this.initialSetup(data);
    this.updating(data);

  }

  render() {
    return <svg ref={this.ref} />;
  }
}

export default PieClass;