import React, { Component } from 'react';
import './App.css';
import {
  HashRouter,
  BrowserRouter as Router,
  Route,
  Switch,
  Link
} from "react-router-dom";
import About from './pages/About.js';
import Education from './pages/Education.js';
import Projects from './pages/Projects.js';
import Experience from './pages/Experience.js';
import Blog from './pages/Blog.js';
import AthleteBrandingComparison from './articles/AthleteBrandingComparison.js'
import GroceryShopping from './articles/GroceryShopping.js'

class App extends Component {
  constructor(props) {
    super(props);
  }


  render() {
    return (
      <HashRouter basename="/" hashType="slash">
        
        <div>
          <Route exact path="/" component={About} />
          <Route exact path="/Education" component={Education} />
          <Route exact path="/Projects" component={Projects} />
          <Route exact path="/Experience" component={Experience} />
          <Route exact path="/Blog" component={Blog} />
          <Route exact path="/Blog/Athlete-Branding-Comparison" component={AthleteBrandingComparison} />
          <Route exact path="/Blog/Grocery-Shopping" component={GroceryShopping} />
        </div>
      </HashRouter>
    )
  }
}

export default App;
