import React, { Component, useRef } from 'react';
import Latex from 'react-latex'

import { Link } from 'react-router-dom';
import './Articles.css';
import './GroceryShopping.css';
import Header from '../pages/Header.js';
import Footer from '../pages/Footer.js';
import * as d3 from "d3";
import Highlight from 'react-highlight'
import './atom-one-light.scss'
import './AthleteBrandingComparison.css';

import ReactMarkdown from 'react-markdown';
import MathJax from 'react-mathjax';
import RemarkMathPlugin from 'remark-math';

import { FaFileExcel } from 'react-icons/fa';

import ArticleImage from "../images/blog/groceryshoppingcropped.jpg";
import PieClass from "./graphs2/PieChart.js";
import PieClass2 from "./graphs2/PieChart2.js";

import tripTotal from "./graphs2/pietotal.csv";
import tripOne from "./graphs2/pie1.csv";
import tripTwo from "./graphs2/pie2.csv";
import tripThree from "./graphs2/pie3.csv";


    
class GroceryShopping extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dataNum: 0,
            color: ['black','black'],
            dataOne: [],
            dataTwo: [],
            dataThree: [],
            dataTotal: []
        }
        this.child = React.createRef();
    }
    

    componentDidMount(){
        this.show(0);
        d3.csv(tripOne, 
            (data) => {
                var curr = []
                var obj = data;
                obj.Index = Number(parseFloat(data.Index).toFixed(2));
                obj.PriceMean = Number(parseFloat(data.PriceMean).toFixed(2));
                obj.PriceTotal = Number(parseFloat(data.PriceTotal).toFixed(2));
                obj.Savings = Number(parseFloat(data.Savings).toFixed(1));
                this.setState(
                  {
                    dataOne: this.state.dataOne.concat(obj)
                  }
                )
        });
        d3.csv(tripTwo, 
          (data) => {
              var curr = []
              var obj = data;
              obj.Index = Number(parseFloat(data.Index).toFixed(2));
              obj.PriceMean = Number(parseFloat(data.PriceMean).toFixed(2));
              obj.PriceTotal = Number(parseFloat(data.PriceTotal).toFixed(2));
              obj.Savings = Number(parseFloat(data.Savings).toFixed(1));
              this.setState(
                {
                  dataTwo: this.state.dataTwo.concat(obj)
                }
              )
        });

        d3.csv(tripThree, 
          (data) => {
              var curr = []
              var obj = data;
              obj.Index = Number(parseFloat(data.Index).toFixed(2));
              obj.PriceMean = Number(parseFloat(data.PriceMean).toFixed(2));
              obj.PriceTotal = Number(parseFloat(data.PriceTotal).toFixed(2));
              obj.Savings = Number(parseFloat(data.Savings).toFixed(1));
              this.setState(
                {
                  dataThree: this.state.dataThree.concat(obj)
                }
              )
        });

        d3.csv(tripTotal, 
          (data) => {
              var curr = []
              var obj = data;
              obj.Index = Number(parseFloat(data.Index).toFixed(2));
              obj.PriceMean = Number(parseFloat(data.PriceMean).toFixed(2));
              obj.PriceTotal = Number(parseFloat(data.PriceTotal).toFixed(2));
              obj.Savings = Number(parseFloat(data.Savings).toFixed(1));
              this.setState(
                {
                  dataTotal: this.state.dataTotal.concat(obj)
                }
              )
        });






    }

    
  
  changeColor(index){
    if(this.state.color[index] == 'black'){
      this.state.color[index] = '#EA5455';
    }else{
      this.state.color[index] = 'black';
    }
    this.forceUpdate();
  }
  show(nr) {
    document.getElementById("table0").style.display="none";
    document.getElementById("table1").style.display="none";
    document.getElementById("table1").style.display="none";
    document.getElementById("table2").style.display="none";
    document.getElementById("table3").style.display="none";
    document.getElementById("table"+nr).style.display="block";

    document.getElementById("grocery-button-0").style.display="block";
    document.getElementById("grocery-button-1").style.display="block";
    document.getElementById("grocery-button-2").style.display="block";
    document.getElementById("grocery-button-3").style.display="block";

    document.getElementById("grocery-button-dark-0").style.display="none";
    document.getElementById("grocery-button-dark-1").style.display="none";
    document.getElementById("grocery-button-dark-2").style.display="none";
    document.getElementById("grocery-button-dark-3").style.display="none";

    document.getElementById("grocery-button-"+nr).style.display="none";
    document.getElementById("grocery-button-dark-"+nr).style.display="block";
  }

  render(){
    var Latex = require('react-latex');
    return(
    <body class="container">
  		<Header pageNum = {5} />
      <MathJax.Provider>
  		<div class="everything">
            <h1 id="article-title">Grocery Shopping Data</h1>
            <img class = "article-image" src={ArticleImage} />
            <div class="article-content">
              <p>During the coronavirus pandemic, I decided to track in detail how much we were spending at the grocery store. Since
                 We try to go out as few times as possible, it was important to understand what we bought during each of our grocery trips. I took
                 this opportunity to practice manipulating datasets using Pandas as well as using D3 for interactive data visualization.
              </p>
              <p>Press one of the following buttons to explore data from different trips</p>
              <div class="column-flex">
                <div class="pie-chart-stuff">
                <button id= "grocery-button-0" class = "grocery-button" onClick={() => {this.child.current.update(0,this.state.dataTotal); this.show(0);}}>Total</button>
                  <button id= "grocery-button-dark-0" class = "grocery-button-dark" onClick={() => {this.child.current.update(0,this.state.dataTotal); this.show(0);}}>Total</button>

                  <button id= "grocery-button-1" class = "grocery-button" onClick={() => {this.child.current.update(1,this.state.dataOne); this.show(1);}}>March 20</button>
                  <button id= "grocery-button-dark-1" class = "grocery-button-dark" onClick={() => {this.child.current.update(1,this.state.dataOne); this.show(1);}}>March 20</button>

                  <button id= "grocery-button-2" class = "grocery-button" onClick={() => {this.child.current.update(2,this.state.dataTwo); this.show(2);}}>March 27</button>
                  <button id= "grocery-button-dark-2" class = "grocery-button-dark" onClick={() => {this.child.current.update(2,this.state.dataTwo); this.show(2);}}>March 27</button>

                  <button id= "grocery-button-3" class = "grocery-button" onClick={() => {this.child.current.update(3,this.state.dataThree); this.show(3);}}>April 3</button>
                  <button id= "grocery-button-dark-3" class = "grocery-button-dark" onClick={() => {this.child.current.update(3,this.state.dataThree); this.show(3);}}>April 3</button>
                  <PieClass2
                    class="piechart"
                    ref={this.child}
                    data={this.state.dataTotal}
                    width={800}
                    height={400}
                    margin={40}
                    innerRadius={0}
                    outerRadius={250}
                    div={"abcd"}
                  />
                  <g class="legend" />
                  
                </div>
                <table id="table0" class = "grocery-data-table">
                  <tr>
                    <th class="grocery-title" colspan="4">Total</th>
                  </tr>
                  <tr class="header-tr">
                    <th>Category</th>
                    <th>Items</th>
                    <th>Mean Price</th>
                    <th>Price Total</th> 
                  </tr>
                  {this.state.dataTotal.map((item) => <tr>
                    <th id="grocery-category">{item.Category}</th>
                    <th>{item.Items.replace("' '",',').replace("\' '",",")}</th>
                    <th>{item.PriceMean}</th>
                    <th>{item.PriceTotal}</th>                             
                  </tr>)}
                </table>
                <table id="table1" class = "grocery-data-table">
                  <tr>
                    <th class="grocery-title" colspan="4">Table One - March 20, 2020</th>
                  </tr>
                  <tr class="header-tr">
                    <th>Category</th>
                    <th>Items</th>
                    <th>Mean Price</th>
                    <th>Price Total</th> 
                  </tr>
                  {this.state.dataOne.map((item) => <tr>
                    <th id="grocery-category">{item.Category}</th>
                    <th>{item.Items.replace('[','').replace('\'','').replace("'","").replace("]","")}</th>
                    <th>{item.PriceMean}</th>
                    <th>{item.PriceTotal}</th>                             
                  </tr>)}
                </table>
                <table id="table2" class = "grocery-data-table">
                  <tr>
                    <th class="grocery-title" colspan="4">Table Two - March 27, 2020</th>
                  </tr>
                  <tr class="header-tr">
                    <th>Category</th>
                    <th>Items</th>
                    <th>Mean Price</th>
                    <th>Price Total</th> 
                  </tr>
                  {this.state.dataTwo.map((item) => <tr>
                    <th id="grocery-category">{item.Category}</th>
                    <th>{item.Items.replace('[','').replace('\'','').replace("'","").replace("]","")}</th>
                    <th>{item.PriceMean}</th>
                    <th>{item.PriceTotal}</th>                             
                  </tr>)}
                </table>
                <table id="table3" class = "grocery-data-table">
                  <tr>
                    <th class="grocery-title" colspan="4">Table Three - April 3, 2020</th>
                  </tr>
                  <tr class="header-tr">
                    <th>Category</th>
                    <th>Items</th>
                    <th>Mean Price</th>
                    <th>Price Total</th> 
                  </tr>
                  {this.state.dataThree.map((item) => <tr>
                    <th id="grocery-category">{item.Category}</th>
                    <th>{item.Items.replace('[','').replace('\'','').replace("'","").replace("]","")}</th>
                    <th>{item.PriceMean}</th>
                    <th>{item.PriceTotal}</th>                             
                  </tr>)}
                </table>
                
                
                
                <div class="hi"></div>
            </div>
          </div>
        </div>
      </MathJax.Provider>
    <Footer />

  </body>
  )
  }
}

export default GroceryShopping;
